n}\n\n.navbar-brand:hover,\n.tm-navbar.scroll .navbar-brand:hover {\n  color: #38B;\n}\n\n.tm-navbar.scroll .navbar-brand {\n  color: #369;\n}\n\n.nav-item {\n  list-style: none;\n}\n\n.tm-nav-link {\n  color: white;\n}\n\n.tm-navbar.scroll .tm-nav-link {\n  color: #369;\n}\n\n.tm-navbar.scroll .tm-nav-link:hover,\n.tm-navbar.scroll .tm-nav-link.current,\n.tm-nav-link:hover {\n  color: #FFF;\n  background-color: #369;\n}\n\n.navbar-toggler {\n  border: 1px solid white;\n  padding-left: 10px;\n  padding-right: 10px;\n}\n\n.navbar-toggler-icon {\n  color: white;\n  padding-top: 6px;\n}\n\n.tm-navbar.scroll .navbar-toggler {\n  border: 1px solid #707070;\n}\n\n.tm-navbar.scroll .navbar-toggler-icon {\n  color: #707070;\n}\n\n/* Hero */\n\n#infinite {\n\tbackground-color: #222;\n  background-image: url(/public/img/infinite-loop-01.jpg);\n  background-repeat: no-repeat;\n  height: 100vh;\n  min-height: 375px;\n  position: relative;\n}\n\n@media (min-height: 600px) and (min-width: 1920px) {\n  #infinite {\n    background-size: cover;\n  }\n}\n\n@media (min-height: 830px) {\n  #infinite {\n    background-position: center -210px;\n  }\n}\n\n@media (min-height: 680px) and (max-height: 829px) {\n  #infinite {\n    background-position: center -300px;\n  }\n}\n\n@media (min-height: 500px) and (max-height: 679px) {\n  #infinite {\n    background-position: center -400px;\n  }\n}\n\n@media (max-height: 499px) {\n  #infinite {\n    background-position: center -450px;\n  }\n}\n\n.tm-hero-text-container {\n  width: 100%;\n  height: 100%;\n  display: flex;\n  flex-flow: column;\n  justify-content: center;\n}\n\n.tm-hero-text-container-inner {\n  margin-top: -90px;\n}\n\n.tm-hero-title {\n  font-size: 3.5rem;\n  text-shadow: 2px 2px 2px #333;\n}\n\n.tm-hero-subtitle {\n  text-shadow: 2px 2px 2px #333;\n}\n\n.tm-intro-next {\n  position: absolute;\n  bottom: 100px;\n  left: 0;\n  right: 0;\n}\n\n@media (max-height: 480px) {\n  .tm-hero-text-container-inner {\n    margin-top: -40px;\n  }\n\n  .tm-intro-next {\n    bottom: 20px;\n  }\n}\n\n.tm-down-arrow-link {\n  display: block;\n  margin-top: 18%;\n}\n\n.tm-down-arrow {\n  \tcolor: #FFF;\n    cursor: pointer;\n    background: #357;\n    padding: 15px 40px;\n    transition: all 0.3s ease;\n}\n\n.tm-down-arrow:hover,\n.tm-down-arrow:focus {\n  color: #FFF;\n  background: #37A;\n  padding: 20px 50px;\n}\n\n/* Introduction */\n\n#introduction {\n  padding-bottom: 100px;\n}\n\n.tm-section-title {\n  font-size: 2.6rem;\n  font-weight: normal;\n}\n\n.tm-intro-text {\n  font-size: 1.2rem;\n}\n\n.tm-icon {\n  display: block;\n  color: #37A;\n}\n\n.tm-continue {\n\tpadding: 20px 0 30px 0;\n}\n\n/* Testimonials */\n#testimonials {\n  color: white;\n  background-image: url(/public/img/infinite-loop-02.jpg);\n  background-size: cover;\n  background-repeat: no-repeat;\n  background-size: 100%;\n  position: relative;\n}\n\n@media (max-width: 991px) {\n  #testimonials {\n    background-image: url(/public/img/infinite-loop-02-mobile.jpg);\n  }\n}\n\n.tm-testimonials-content {\n  position: relative;\n  z-index: 100;\n}\n\n.tm-bg-overlay {\n  width: 100%;\n  height: 100%;\n  background: rgba(20, 70, 80, 0.2);\n  position: absolute;\n  top: 0;\n  left: 0;\n  right: 0;\n  bottom: 0;\n  z-index: 0;\n}\n\n.tm-testimonials-carousel {\n  max-width: 1050px;\n  margin: 0 auto;\n}\n\n.tm-testimonial-item {\n  max-width: 290px;\n  margin-left: 35px;\n  margin-right: 35px;\n  list-style-image: url(/public/img/testimonial-img-02.jpg);\n}\n\n.tm-testimonial-item img {\n  border-radius: 50%;\n  margin-bottom: 35px;\n}\n\n\n.tm-testimonial-item figcaption {\n  text-align: right;\n  font-style: italic;\n  font-size: 1.1rem;\n}\n\n/* Work */\n\n.tm-section-desc {\n  max-width: 650px;\n  width: 100%;\n  font-size: 0.9rem;\n}\n\n.tm-gallery-container {\n  padding-top: 70px;\n  padding-bottom: 120px;\n}\n\n.tm-gallery-item {\n  margin: 0 15px;\n}\n\n.slick-dots {\n  bottom: -65px;\n}\n\n.slick-dots li {\n  margin: 0 13px;\n}\n\n.slick-